@use 'var';
.header {
    position: sticky;
    top: 0;
    left: 0;
    background-color: var.$corDeFundo;
    transition: background-color 0.5s ease;
    z-index: 1;
  
    &--is-hidden {
      background-color: transparent;
      transition: background-color 0.5s ease;
  
      .header__logo,
      .header__links__item:first-child {
        opacity: 0;
        visibility: hidden;
        transition: opacity 0.5s ease;
      }
    }
  
    &__container {
      padding: 8px 36px;
      display: flex;
      justify-content: space-between;
    }
  
    &__logo {
      max-width: 80px;
      width: 100%;
      margin: 5px;
      transition: opacity 0.5s ease;
    }
  
    &__links {
      display: flex;
  
      &__item {
        margin: 5px;
        transition: opacity 0.5s ease;
  
        a {
          text-decoration: none;
        }
      }
    }
  
    .button {
      height: 50px;
    }
  }
  